














import { defineComponent, ref } from '@vue/composition-api';
import Picture from './Picture.vue';

const PicCarousel = defineComponent({
  name: 'PicCarousel',
  components: { Picture },
  props: {
    editable: {
      type: Boolean,
      default: false,
    },

    children: {
      type: Array,
      // default: () => [],
    },
  },
  setup(props, { emit }) {
    const autoplay = ref(true);
    function onChange(index: number, event: any) {
      event.path.unshift(index);
      event.path.unshift('children');
      emit('change', event);
    }

    return {
      onChange,
      autoplay,
    };
  },
});
export default PicCarousel;
