







import { defineComponent } from '@vue/composition-api';
import Banner from './Banner.vue';
import PicSeries from './PicSeries.vue';
import PicCarousel from './PicCarousel.vue';

const Page = defineComponent({
  name: 'Page',
  components: {
    Banner,
    PicSeries,
    PicCarousel,
  },
  props: {
    editable: {
      type: Boolean,
      default: false,
    },

    children: {
      type: Array,
      // default: () => [],
    },
  },
  setup(props, { emit }) {
    function onChange(index: number, event: any) {
      event.path.unshift(index);
      event.path.unshift('children');
      emit('change', event);
    }

    return {
      onChange,
    };
  },
});
export default Page;
