





import {
  defineComponent, ref, Ref, reactive,
} from '@vue/composition-api';
import { getCompanyHome } from '@/api/company';
import useLoader from '_/composables/use-loader';
import Page from './modules/Page.vue';

const HomePageView = defineComponent({
  name: 'HomePageView',
  components: {
    Page,
  },
  props: {
    companyId: {
      type: String,
      default: '',
    },
  },
  setup(props, { emit }) {
    const pageConfig = useLoader(async () => {
      const defaultCfg = {
        type: 'Page',
        version: '1.0.1',
        children: [
          {
            type: 'Banner',
            image: undefined,
            link: {
              type: 0,
            },
          },
          {
            type: 'PicSeries',
            children: [
              {
                type: 'Picture',
                image: undefined,
                link: {
                  type: 1,
                },
              },
              {
                type: 'Picture',
                image: undefined,
                link: {
                  type: 2,
                },
              },
              {
                type: 'Picture',
                image: undefined,
                link: {
                  type: 3,
                },
              },
            ],
          },
          {
            type: 'PicCarousel',
            children: [
              {
                type: 'Picture',
                image: undefined,
                link: {
                  type: 4,
                },
              },
              {
                type: 'Picture',
                image: undefined,
                link: {
                  type: 5,
                },
              },
            ],
          },
        ],
      };
      const res = (await getCompanyHome(props.companyId)).data;
      if (res) {
        const resData = JSON.parse(res);
        if (resData && resData.version !== '1.0.1') {
          return defaultCfg;
        }
        return resData;
      }
      return defaultCfg;
    }, {}, {
      initialValue: null,
    });
    return {
      pageConfig,
    };
  },
});
export default HomePageView;
