
















import { defineComponent, ref } from '@vue/composition-api';
import UploadHomePageImg from '@/views/supplier-backend/components/UploadHomePageImg.vue';
import { i18n } from '@/i18n';

interface Link {
  type: 0 | 1 | 2 | 3 | 4 | 5,
}

const Banner = defineComponent({
  name: 'Banner',
  components: {
  },
  props: {
    editable: {
      type: Boolean,
      default: false,
    },

    image: {
      type: String,
      default: i18n.language === 'cn' ? require('@/assets/img/material-home/banner-default.png')
        : require('@/assets/img/material-home/banner-default-en.png'),
    },
    link: {
      type: Object as () => Link,
      default: null,
    },
  },
  setup(props, { emit, root: { $injectDialog } }) {
    // setTimeout(() => {
    //   emit('change', { path: ['image'], value: bannerDefault });
    // }, 2000);
    async function showUpload() {
      const { field, value } = (await $injectDialog({
        is: UploadHomePageImg,
        props: {
          url: props.image,
          maxSize: 4 * 1024 * 1024,
          sizeTips: i18n.t('supplier-backend.homepage.upload_img_tip', ['1740*830px', '4MB']),
        },
      }) as { field: string, value: string });
      emit('change', { path: ['image'], value });
    }
    return {
      // changeBanner,
      showUpload,
    };
  },
});
export default Banner;
